import React, {useContext, useEffect, useState} from 'react'
import {BackGroundDecor} from '../../components/BackGroundDecor/BackGroundDecor'
import {BreadCrumbs} from '../../components/BreadCrumbs/BreadCrumbs'
import {CartList} from '../../components/CartList/CartList'
import {SectionTitle} from '../../components/SectionTitle/SectionTitle'
import {useSelector} from 'react-redux'
import {PopupContext} from "../../App";
import axios from "axios";
import {getApiLink} from "../../api/getApiLink";
import {GetApiHeaders} from "../../functions/getApiHeaders";

export const Basket = () => {
    const setModal = useContext(PopupContext)

    const [totalAmount, setTotalAmount] = useState(0)
    const [products, setProducts] = useState([])

    const basketList = useSelector(state => state.toolkit.basket);

    const handleOpenModal = (type) => {
		setModal(`${type}`)
    }

    useEffect(() => {
        axios.get(getApiLink('/api/products/get'), {headers: GetApiHeaders()})
            .then(({data}) => {

                setProducts(data.data)

                setTotalAmount(0)

                const total = data.data?.filter(item => basketList.some(item2 => item.id === item2.product_id)).reduce((amount, current) => {
                    amount += current.price
                    return amount
                }, 0)

                setTotalAmount(total)

            })
            .catch((error) => {
                console.log('all prod undefined', error);
            })
    }, [basketList])

    return (
        <>
            <BackGroundDecor/>

            <BreadCrumbs pages={[{page: ''}]}/>

            <section className="cart container">

                <SectionTitle title={''}/>

                <div className="cart__note">
                    <p>
                                   -
                        <button onClick={_ => handleOpenModal("register")} className="open-popup"></button> 
                        <button onClick={_ => handleOpenModal("login")} className="open-popup"></button>   ,  
                          
                    </p>
                </div>

                <CartList setTotalAmount={setTotalAmount} products={products}/>

                <div className="cart__footer">
                    <div className="cart__footer_col">
                        <div className="cart__message">
                            <label htmlFor="order-message">  </label>
                            <p><textarea name="order-message" placeholder=" " id="order-message"></textarea>
                            </p>
                        </div>
                    </div>
                    <div className="cart__footer_col">
                        <table className="cart__total">
                            <tbody>
                            <tr>
                                <td>  </td>
                                <td>0 </td>
                            </tr>
                            <tr className="add-hr">
                                <td>
                                    <b></b>
                                </td>
                                <td>
                                    <strong data-price-sum-result="cart-list" data-price-discount="500" data-price-currency="">
                                        {totalAmount}
                                    </strong>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    
                                </td>
                                <td>
                                    0 
                                </td>
                            </tr>
                            </tbody>
                        </table>
                        <label className="cart__checkbox checkbox">
                            <input type="checkbox" name="receive-docs" className="checkbox-input"/>
                            <span className="checkbox-element">
							<svg width="17" height="17" viewBox="0 0 17 17">
								<use xlinkHref="#check"></use>
							</svg>
						</span>
                            <span className="checkbox-text">
							          
						</span>
                        </label>

                        <button onClick={_ => handleOpenModal('order')} className="cart__submit button is-mode-1 open-popup" data-href="#order-popup"
                                type="button" aria-label=" ">
                            <svg width="24" height="24" viewBox="0 0 48 48">
                                <use xlinkHref="#cart"></use>
                            </svg>
                            <span>
                                 
                            </span>
                        </button>
                    </div>
                </div>
            </section>
        </>
    )
}
